
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Checklist from 'vux/src/components/checklist/index.vue'
import Calendar from 'vux/src/components/calendar/index.vue'
import PopupHeader from 'vux/src/components/popup-header/index.vue'

export default {
components: {
XInput,PopupHeader,
XButton,
Group,
Cell,
Checklist,
Calendar
},
data () {
return {
f:[],
dingwei:'...',
commonList: [
'(07:00 - 11:00)',
'(11:00 - 15:00)',
'(15:00 - 19:30)',
'(19:30 - 23:00)'
]
}
},
created(){
let _ = this;
setTimeout(()=>{
_.dingwei = "."
},700)
setTimeout(()=>{
_.dingwei = ".."
},1400)
setTimeout(()=>{
_.dingwei = "..."
},2100)
setTimeout(()=>{
_.dingwei = ""
},3000)
},
methods: {
comfirm(){
const _this = this 
if(!(_this.f.xm && _this.f.xb && _this.f.dh && _this.f.d && _this.f.t.length)){
this.$vux.alert.show({
title: '',
content: '',
})
return;
}
this.$vux.confirm.show({
title: '',
content: _this.f.xm + "",
onCancel () {

},
onConfirm () {
_this.$vux.loading.show({
text: 'Loading'
})
setTimeout(() => {
_this.$vux.loading.hide()
_this.$router.push("/success")
}, 1500)
}
})
}
}
}



